
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
  name: 'marklist-view',
  components: { 
  	Swipeout, 
    SwipeoutItem, 
    SwipeoutButton,
    XInput,
    XButton,
    Group
  },
  data () {
    return {
      allTask:[
        {
          title:'',
          id:1
        },
        {
          title:'',
          id:2
        },
        {
          title:'',
          id:3
        },
        {
          title:'',
          id:4
        },

        {
          title:'',
          id:5
        },
        {
          title:'',
          id:6
        },
        {
          title:'',
          id:7
        }
      ],
      txtValue:''
    }
  },
  computed: {
  	
  },
  filter:{
  	
  },
  methods: {
  	onButtonClick (type) {
      alert('on button click ' + type)
    },
    handleEvents (type) {
      console.log('event: ', type)
    },
    xtrim (str) {
			return str.replace(/(^\s*)|(\s*$)/g, ""); 	 //
		},
    onBtnClick () {
    	if(!this.xtrim(this.txtValue)){
    		alert('');
    		return;
    	}
    	var nowId = this.allTask.length + 1;
    	this.allTask.push({
        title:this.xtrim(this.txtValue),
        id:nowId
      });
      this.txtValue = '';
      vm.$data.allT = this.$data.allTask;
    }
  },
  created: function () {
  	this.$nextTick(function(){
  		this.$data.allTask = vm.$data.allT;
  	})
  }
}



